import React from "react";
import PropTypes from "prop-types";
import Anchor from "components/Anchor";
import { Header, NoStylesBody } from "components/Typography";

const CommunityCard = ({ title, meetLink, applyLink, children }) => (
  <div className="p-8 shadow-custom flex-1">
    <Header tag="h4">{title}</Header>
    <div className="pb-4">
      <NoStylesBody>{children}</NoStylesBody>
    </div>
    <Anchor to={meetLink} color="secondary">
      Meet {title}
    </Anchor>
    <br />
    <Anchor to={applyLink} color="secondary">
      Become a {title.substring(0, title.length - 1)}
    </Anchor>
  </div>
);
CommunityCard.propTypes = {
  title: PropTypes.string.isRequired,
  children: PropTypes.string.isRequired,
  meetLink: PropTypes.string.isRequired,
  applyLink: PropTypes.string.isRequired,
};

export const NoMeetCommunityCard = ({ title, applyLink, children }) => (
  <div className="p-8 shadow-custom flex-1">
    <Header tag="h4">{title}</Header>
    <div className="pb-4">
      <NoStylesBody>{children}</NoStylesBody>
    </div>
    <br />
    <Anchor to={applyLink} color="secondary">
      Become a {title.substring(0, title.length - 1)}
    </Anchor>
  </div>
);
NoMeetCommunityCard.propTypes = {
  title: PropTypes.string.isRequired,
  children: PropTypes.string.isRequired,
  applyLink: PropTypes.string.isRequired,
};

export default CommunityCard;
