import React from "react";
import PropTypes from "prop-types";

// Since the width is based off height.
const Line = () => (
  <div className="px-7.5 md:px-16 hidden sm:flex items-end order-1 sm:order-2">
    <div className="h-11/20 bg-offBlack w-4px flex flex-col items-center">
      <PrimaryDot />
    </div>
  </div>
);
export default Line;

export const MobileLine = () => (
  <div className="px-7.5 md:px-16 flex sm:hidden items-end order-1 sm:order-2">
    <div className="h-full bg-offBlack w-4px flex flex-col items-center">
      <div className="h-1/2">
        <div className="h-1/2 bg-offWhite w-4px flex flex-col items-center justify-end">
          <PrimaryDot />
        </div>
      </div>
    </div>
  </div>
);

export const MiddleLine = ({ primary }) => (
  <div
    className={`px-7.5 md:px-16 hidden sm:items-end order-1 sm:flex ${
      primary ? "sm:order-3" : "sm:order-2"
    }`}
  >
    <div className="h-full bg-offBlack w-4px flex flex-col items-center justify-center">
      {primary ? <PrimaryDot /> : <SecondaryDot />}
    </div>
  </div>
);
MiddleLine.propTypes = {
  primary: PropTypes.bool,
};
MiddleLine.defaultProps = {
  primary: true,
};

export const MobileMiddleLine = ({ primary }) => (
  <div className="px-7.5 md:px-16 flex sm:items-end order-1 sm:order-2 sm:hidden">
    <div className="h-full bg-offBlack w-4px flex flex-col items-center justify-center">
      <div className="h-1/2 ">
        {primary ? <PrimaryDot /> : <SecondaryDot />}
      </div>
    </div>
  </div>
);
MobileMiddleLine.propTypes = {
  primary: PropTypes.bool,
};
MobileMiddleLine.defaultProps = {
  primary: true,
};

export const EndLine = () => (
  <div className="px-7.5 md:px-16 hidden sm:flex order-2">
    <div className="h-7/12 bg-offBlack w-4px flex flex-col items-center justify-end">
      <PrimaryDot />
    </div>
  </div>
);

export const MobileEndLine = () => (
  <div className="px-7.5 md:px-16 flex order-1 sm:order-2 sm:hidden">
    <div className="h-1/2 bg-offBlack w-4px flex flex-col items-center justify-end">
      <div className="h-1/2 bg-offWhite w-4px flex flex-col items-center">
        <PrimaryDot />
      </div>
    </div>
  </div>
);

export const PrimaryDot = () => (
  <div className="bg-primary w-8 h-8 rounded-full" />
);

export const SecondaryDot = () => (
  <div className="bg-secondary w-8 h-8 rounded-full" />
);
