// imports all the images from the folder /staffPage
function importAll(r) {
  const keys = r.keys().map(r);
  const names = r.keys().map((key) => {
    const removedFront = key.replace("./", "");
    const afterPeriod = removedFront.indexOf(".");
    const retVal = removedFront.substring(0, afterPeriod);
    return retVal;
  });

  // combine both arrays
  const images = {};
  for (let i = 0; i < names.length; i += 1) {
    images[names[i]] = keys[i];
  }
  return images;
}
const images = importAll(
  require.context(
    "assets/images/staffPage/leadership",
    false,
    /\.(png|jpe?g|svg)$/
  )
);

