/* eslint-disable no-nested-ternary */
import React from "react";
import PropType from "prop-types";
import staff from "./staff";
import mentors from "./mentors";
import Card from "./Card";

const Cards = ({ type }) => (
  <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-8 mt-16">
    {type === "staff" ? (
      staff.map(({ id, name, occupation, image, bio }) => (
        <Card key={id} name={name} occupation={occupation} src={image}>
          {bio}
        </Card>
      ))
    ) : type === "mentors" ? (
      mentors.map(({ id, name, occupation, image, bio }) => (
        <Card key={id} name={name} occupation={occupation} src={image}>
          {bio}
        </Card>
      ))
    ) : (
      <div />
    )}
  </div>
);
Cards.propTypes = {
  type: PropType.string.isRequired,
};

export default Cards;
